import React, { Component } from "react";
// import data from "../../data/social-media-data.js";
import fiverr from "../../data/icons/Fiverr-Logo.png";
import linkedIn from "../../data/icons/icons8-linkedin-50.png";
import youtube from "../../data/icons/icons8-play-button-50.png";
import github from "../../data/icons/icons8-github-50.png";

import "./social-media-nav.css";

class SocialMediaNav extends Component {
	constructor(props) {
		super(props);
		this.state = {
			isFooter: false,
		};
	}

	componentDidMount() {
		if (this.props.isFooter)
			this.setState({ isFooter: this.props.isFooter });
	}
	render() {
		// console.log({ data });
		return (
			<>
				{!this.state.isFooter ? (
					<nav className="social-nav-btns">
						<a href="https://www.fiverr.com/milesoluku">
							<img src={fiverr} />
						</a>
						<a href="">
							<img src={linkedIn} />
						</a>
						<a href="">
							<img src={youtube} />
						</a>
						<a href="https://github.com/milesuno">
							<img src={github} />
						</a>
					</nav>
				) : (
					<nav className="social-nav-btns">
						<a href="mailto:milesoluku@gmail.com?subject=Portfolio Enquires">
							<img
								alt="svgImg"
								src="data:image/svg+xml;base64,"
							/>
						</a>
						<a href="">
							<img
								alt="svgImg"
								src="data:image/svg+xml;base64,"
							/>
						</a>
						<a href="">
							<img
								alt="svgImg"
								src="data:image/svg+xml;base64,"
							/>
						</a>
						<a href="https://github.com/milesuno">
							<img
								alt="svgImg"
								src="data:image/svg+xml;base64,"
							/>
						</a>
					</nav>
				)}
			</>
		);
	}
}

export default SocialMediaNav;
